<template>
    <div class="aside-menu">
    <el-menu
            :default-active="activePath"
            class="el-menu-vertical-demo"
            style="min-height: calc(100vh - 50px);"
            router
    >
        <el-menu-item index="home" @click="saveNavState('home')">
            <el-icon>
                <home-filled />
            </el-icon>
            <span>Home</span>
        </el-menu-item>
        <el-menu-item index="search" @click="saveNavState('search')">
            <el-icon>
                <search />
            </el-icon>
            <span>Search</span>
        </el-menu-item>
        <el-sub-menu index="browse">
            <template #title>
                <el-icon>
                    <icon-menu/>
                </el-icon>
                <span>Browse</span>
            </template>
            <el-menu-item index="overview" @click="saveNavState('overview')">Overview</el-menu-item>
            <el-menu-item index="gwas" @click="saveNavState('gwas')">GWAS</el-menu-item>
            <el-menu-item index="eqtl" @click="saveNavState('eqtl')">eQTL</el-menu-item>
            <el-menu-item index="meqtl" @click="saveNavState('meqtl')">meQTL</el-menu-item>
            <el-menu-item index="emqtl" @click="saveNavState('emqtl')">emQTL</el-menu-item>
            <el-menu-item index="ewas" @click="saveNavState('ewas')">EWAS</el-menu-item>
        </el-sub-menu>
        <el-menu-item index="download" @click="saveNavState('download')">
            <el-icon>
                <download />
            </el-icon>
            <span>Download</span>
        </el-menu-item>
        <el-menu-item index="help" @click="saveNavState('help')">
            <el-icon>
                <question-filled />
            </el-icon>
            <span>Help</span>
        </el-menu-item>
    </el-menu>
    </div>
</template>

<script>
    import {
        HomeFilled,
        Menu as IconMenu,
        Search,
        Download,
        QuestionFilled,

    } from '@element-plus/icons-vue'



    export default {
        name: "AsideMenu",
        components:{
            HomeFilled,
            IconMenu,
            Search,
            Download,
            QuestionFilled,

        },
        methods:{
            saveNavState(activePath){
                window.sessionStorage.setItem('activePath', activePath)
                this.activePath = window.sessionStorage.getItem('activePath')
            }
        },
        created() {
          this.activePath = window.sessionStorage.getItem('activePath')
        },
        data() {
            return {
                activePath: ''
            }
        }

    }


</script>

<style scoped>
    .aside-menu{
        width: 150px;
    }

    .el-sub-menu .el-menu-item {
        min-width: 150px;
    }

</style>
